'use client'
import { useState } from "react";
import { students } from "@/app/demo/list";
const AdminPage = () => {
    const [studentId, setStudentId] = useState('');
    const [admin, setAdmin] = useState<null | typeof students>(null);
    const [error, setError] = useState('');
    let adminKey = 'Password';
    const handleAdmin = (e: React.FormEvent) => {
        e.preventDefault();
        // const allFound = students.map(std => std)
        
        if (adminKey == studentId) {
            // setAdmin(students)
            // setError('');
            alert()
        }
        else {
            setError('No users found')
        }
    }
    return (
        <>
            <div>
                <h1 className="text-2xl font-bold mb-4">
                    Student access
                </h1>
                <form onSubmit={AdminPage} className="space-y-4">
                    <input type="text" placeholder="Enter your student id" value={studentId} onChange={(e) => setStudentId(e.target.value)} className="w-full p-2 border rounded" />
                    <button type="submit" className="w-full bg-blue-600 text-white p-2 rounded hover:bg-blue-700">View Info</button>
                </form>
                {error && <p className="text-red-500">{error}</p>}
                {admin && (
                    <div className="ml-30">
                        {
                            admin.map(std => (

                                <p><strong>Name:</strong> {std.name} <strong>Age:</strong>{std.age} <strong>Class:</strong>{std.class} <strong>Id:</strong>{std.id}</p>
                            ))
                        }
                    </div>
                )}
            </div>
        </>
    );
}

export default AdminPage;